import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
//Popol start 레이아웃

const Body = styled.div`
    width:100%;
    height:auto;
    display:flex;
`;

const Section1 = styled.div`
    width:70%;
    height:auto;
    margin:auto;
    background-color:rgb(230,230,230);
`;

const Section2 = styled.div`
    width:10%;
    height:auto;
`;

const Paper = styled.div`
    width:100%;
    height: calc(${window.innerHeight}px - 100px);
    margin-top:100px;
    display: flex;
    flex-wrap: wrap;
`;

const Paperpicture = styled.div`
    width:30%;
    height:65%;
`;

const Skillarea = styled.div`
    width:70%;
    height:65%;
    display: flex;
    flex-wrap:wrap;
`;

const Useskill = styled.div`
    width:100%;
    height:calc(35%);
    display: flex;
    flex-wrap:wrap;
`;

const Picture = styled.div`
    width:100%;
    height: 80%;
    margin:auto;
    background-image: url(${require('../../assets/user.png').default});
    background-size:cover;
`;

const Skill = styled.div`
    width:100%;
    height:100%;
    background-image:url(${props => props.BackImg});
    background-position: center;
    background-size: ${props => props.BackSize};
    background-repeat: no-repeat;
    position: relative;
    right: -100%;
    opacity: 0;
    font-size:40px;
    color:rgb(0,0,0);
    font-weight: 900;
    transition:all 0.4s ${props => props.delay};
`;

const Skillbox = styled.div`
    width: 33.333%;
    height:50%;
    overflow: hidden;
`;

const Span = styled.span`
    position: relative;
    top: 25px;
    color:black;
`;

const Stick = styled.div`
    width:80%;
    height:40px;
    border:2px solid black;
    border-radius: 5px;
    position: relative;
    top:50px;
    margin:auto;
`;

const Result = styled.div`
    width:${props => props.Width};
    height:100%;
    background-color:#3fce02;
    border-radius: 2px;
    transition:1.7s ${props => props.delay};
`;

const Resulttxt = styled.span`
    position: absolute;
    text-align:center;
    color:black;
    text-indent:-10px;
    transform: translateY(7px);
    font-size:20px;
    transition: 1s ${props => props.delay};
`;

const Intro = styled.div`
    width:100%;;
    height:calc(${window.innerHeight}px - 100px);
    margin-top:100px;
    display:flex;
    flex-wrap:wrap;
    overflow: hidden;
`;

const EndBack = styled.div`
    width:calc(25%);
    height:25%;
    background-image:url(${props => (props.list)});
    background-size:cover;
    background-position: center;
    margin: 0px 0px;
`;

const MoRa = styled.div`
    width:calc(${window.innerWidth}px * 7 / 10 * 1 / 4 );
    height:calc((${window.innerHeight}px - 100px) * 7 / 10 - 100px);
    display: inline-block;
    position: relative;
    left:${props => props.x};
    top:${props => props.y};
    z-index:${props => props.index};
    transform:${props => props.index === "3" ? null :
        props.index === "2" ? "scale(0.9)" : "scale(0.8)"};
    box-shadow:${props => props.index === "1" ? null :
        "1px 1px 3px rgba(20,20,20,0.5),2px 2px 3px rgba(20,20,20,0.5),3px 3px 3px gray,4px 4px 3px gray"
    };
    background-image: url(${require("../../assets/sample.jpg").default});
    background-size:cover;
    transition:1s all;
    opacity:${props => props.opacity};
    &&:hover{
        transform:${props => props.index === "3" ? "scale(1.02)" :
        props.index === "2" ? "scale(0.92)" : "scale(0.82)"};
        opacity: 1;
    }
`;

const Reactflix = styled.div`
    width:100%;
    height:100%;
    background-image: url(${require("../../assets/sample.jpg").default});
    background-size:cover;
`;

const TxtEvent = () => {
    const delay = 4200;
    const [state, setState] = useState({
        sumtxt: "유",
    })

    useEffect(() => {

        setTimeout(() => {
            setState({
                sumtxt: "유 ",
            })
        }, delay * 1 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현",
            })
        }, delay * 2 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준",
            })
        }, delay * 3 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준 입",
            })
        }, delay * 4 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준 입니",
            })
        }, delay * 5 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준 입니다",
            })
        }, delay * 6 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준 입니다.",
            })
        }, delay * 7 / 27 + 200)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준 입니다",
            })
        }, delay * 8 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준 입니",
            })
        }, delay * 9 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준 입",
            })
        }, delay * 10 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현 준",
            })
        }, delay * 11 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유 현",
            })
        }, delay * 12 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "유",
            })
        }, delay * 13 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준 ",
            })
        }, delay * 14 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비",
            })
        }, delay * 15 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가",
            })
        }, delay * 16 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되",
            })
        }, delay * 17 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어",
            })
        }, delay * 18 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있",
            })
        }, delay * 19 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는",
            })
        }, delay * 20 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는 유",
            })
        }, delay * 21 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는 유현",
            })
        }, delay * 22 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는 유현준",
            })
        }, delay * 23 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는 유현준 입",
            })
        }, delay * 24 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는 유현준 입니",
            })
        }, delay * 25 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는 유현준 입니다",
            })
        }, delay * 26 / 27)

        setTimeout(() => {
            setState({
                sumtxt: "준비가 되어있는 유현준 입니다.",
            })
        }, delay)

    }, [])

    return state;
}

const UseInfo = ({ Delay = 'number' }) => {
    let start = null;
    let progress;
    const html5 = 95;
    const css3 = 95;
    const javascript = 40;
    const jquery = 80;
    const php = 30;
    const react = 30;

    const [state, setState] = useState({
        opacity: 0,
        right: 100,
        delay: 0,
        html5: 0,
        css3: 0,
        javascript: 0,
        jquery: 0,
        php: 0,
        react: 0,
        hvalue: 0,
        cvalue: 0,
        jvalue: 0,
        qvalue: 0,
        pvalue: 0,
        rvalue: 0,
    })

    useEffect(() => {

        setTimeout(() => {
            setState({
                opacity: 1,
                right: 0,
                delay: Delay,
                html5: html5,
                css3: css3,
                javascript: javascript,
                jquery: jquery,
                php: php,
                react: react,
                hvalue: 0,
                cvalue: 0,
                jvalue: 0,
                qvalue: 0,
                pvalue: 0,
                rvalue: 0,
            })
        }, 0)

        setTimeout(() => {
            const step = timestamp => {

                if (!start) {
                    start = timestamp;
                }
                progress = (timestamp - start) / 15;

                setState({
                    opacity: 1,
                    right: 0,
                    delay: Delay,
                    html5: html5,
                    css3: css3,
                    javascript: javascript,
                    jquery: jquery,
                    php: php,
                    react: react,
                    hvalue: (progress >= html5) ? html5 : parseInt(progress),
                    cvalue: (progress >= css3) ? css3 : parseInt(progress),
                    jvalue: (progress >= javascript) ? javascript : parseInt(progress),
                    qvalue: (progress >= jquery) ? jquery : parseInt(progress),
                    pvalue: (progress >= php) ? php : parseInt(progress),
                    rvalue: (progress >= react) ? react : parseInt(progress),
                })

                if (progress >= 95) {
                    return;
                }

                window.requestAnimationFrame(step);
            }
            window.requestAnimationFrame(step);
        }, Delay * 1000)
    }, [])

    const img = [
        require("../../assets/1.jpg").default,
        require("../../assets/2.jpg").default,
        require("../../assets/3.jpg").default,
        require("../../assets/4.jpg").default,
        require("../../assets/5.jpg").default,
        require("../../assets/6.jpg").default,
        require("../../assets/7.jpg").default,
        require("../../assets/8.jpg").default,
        require("../../assets/9.jpg").default,
        require("../../assets/10.jpg").default,
        require("../../assets/11.jpg").default,
        require("../../assets/12.jpg").default,
        require("../../assets/13.jpg").default,
        require("../../assets/14.jpg").default,
        require("../../assets/15.jpg").default,
        require("../../assets/16.jpg").default,
    ];

    const { sumtxt } = TxtEvent();

    return <>
        <Body>
            <Section1>
                <Paper>
                    <Paperpicture>
                        <Picture style={{ opacity: state.opacity }} />
                        <div style={{ color: "black", fontSize: "20px", fontWeight: "600", marginTop: "40px" }}>{sumtxt}</div>
                        <div style={{ color: "black", marginTop: "30px", textAlign: "left", textIndent: "20px", fontWeight: "600" }}>| 프로그램 활용도 |</div>
                    </Paperpicture>
                    <Skillarea>
                        <Skillbox>
                            <Skill
                                BackImg={require('../../assets/HTML5-logo.png').default}
                                BackSize={'95% 85%'}
                                delay={`${state.delay * 1 / 6}s`}
                                style={{ opacity: state.opacity, right: state.right }}
                            />
                        </Skillbox>
                        <Skillbox>
                            <Skill BackImg={require('../../assets/CSS3-logo.png').default}
                                BackSize={'55% 85%'}
                                delay={`${state.delay * 2 / 6}s`}
                                style={{ opacity: state.opacity, right: state.right }}
                            />
                        </Skillbox>
                        <Skillbox>
                            <Skill BackImg={require('../../assets/js-logo.png').default}
                                BackSize={'85% 85%'}
                                delay={`${state.delay * 3 / 6}s`}
                                style={{ opacity: state.opacity, right: state.right }}
                            />
                        </Skillbox>
                        <Skillbox>
                            <Skill BackImg={require('../../assets/jQuery-logo.png').default}
                                BackSize={'85% 95%'}
                                delay={`${state.delay * 4 / 6}s`}
                                style={{ opacity: state.opacity, right: state.right }}
                            />
                        </Skillbox>
                        <Skillbox>
                            <Skill BackImg={require('../../assets/PHP-logo.png').default}
                                BackSize={'85% 65%'}
                                delay={`${state.delay * 5 / 6}s`}
                                style={{ opacity: state.opacity, right: state.right }}
                            />
                        </Skillbox>
                        <Skillbox>
                            <Skill
                                BackImg={require('../../assets/React-logo.png').default}
                                BackSize={'75% 80%'}
                                delay={`${state.delay}s`}
                                style={{ opacity: state.opacity, right: state.right }}
                            >
                                React
                            </Skill>
                        </Skillbox>
                    </Skillarea>
                    <Useskill>
                        <Skillbox>
                            <Span> HTML5 </Span>
                            <Stick>
                                <Resulttxt delay={state.delay}>{`${state.hvalue}%`}</Resulttxt>
                                <Result
                                    Width={`${state.html5}%`}
                                    delay={`${state.delay}s`}
                                />
                            </Stick>
                        </Skillbox>
                        <Skillbox>
                            <Span> CSS3 </Span>
                            <Stick>
                                <Resulttxt>{`${state.cvalue}%`}</Resulttxt>
                                <Result
                                    Width={`${state.css3}%`}
                                    delay={`${state.delay}s`}
                                />
                            </Stick>
                        </Skillbox>
                        <Skillbox>
                            <Span> JavaScript </Span>
                            <Stick>
                                <Resulttxt>{`${state.jvalue}%`}</Resulttxt>
                                <Result
                                    Width={`${state.javascript}%`}
                                    delay={`${state.delay}s`}
                                />
                            </Stick>
                        </Skillbox>
                        <Skillbox>
                            <Span> jQuery </Span>
                            <Stick>
                                <Resulttxt>{`${state.qvalue}%`}</Resulttxt>
                                <Result
                                    Width={`${state.jquery}%`}
                                    delay={`${state.delay}s`}
                                />
                            </Stick>
                        </Skillbox>
                        <Skillbox>
                            <Span> PHP </Span>
                            <Stick>
                                <Resulttxt>{`${state.pvalue}%`}</Resulttxt>
                                <Result
                                    Width={`${state.php}%`}
                                    delay={`${state.delay}s`}
                                />
                            </Stick>
                        </Skillbox>
                        <Skillbox>
                            <Span> React </Span>
                            <Stick>
                                <Resulttxt>{`${state.rvalue}%`}</Resulttxt>
                                <Result
                                    Width={`${state.react}%`}
                                    delay={`${state.delay}s`}
                                />
                            </Stick>
                        </Skillbox>
                    </Useskill>
                </Paper>
                <Intro>
                    <div style={{ width: "100%", height: "70%" }}>{/* 클릭이벤트로 x값을 변경하고 변경에따라 ? null:null y,index,opacity변경 */}
                        <MoRa x={"7%"} y={"50px"} index={"2"} opacity={0.7} />{/*37(1&&4) 7(좌2) 67(우2) */}
                        <MoRa x={"12%"} y={"100px"} index={"3"} opacity={1} />{/*12(1&&4) -18(좌2) 42(우2) */}
                        <MoRa x={"17%"} y={"50px"} index={"2"} opacity={0.7} />{/*-13(1&&4) -43(좌2) 17(우2)*/}
                        <MoRa x={"-38%"} y={"0px"} index={"1"} opacity={0.5} />{/*-38(1&&4) -68(좌2) -8(우2) */}
                    </div>
                    <div style={{ width: "100%", height: "30%", padding: "10px" }}>MoRa 프로젝트 설명</div>
                </Intro>
                <Intro>
                    <div style={{ width: "60%", height: "100%", textAlign: 'left', display: "flex", flexWrap: "wrap" }}>
                        <div style={{ width: "100%", height: "10%", backgroundColor: "red" }}></div>
                        <div style={{ width: "100%", height: "80%", overflow: "hidden" }}>

                        </div>
                        <div style={{ width: "100%", height: "10%", backgroundColor: "red" }}></div>
                    </div>
                    <div style={{ width: "40%", height: "100%", textAlign: 'left' }}>
                        <Link to="/react-flix">React-flix 클론 코딩 설명(Link)</Link>
                    </div>
                </Intro>
                <Intro>
                    <div style={{ width: "100%", height: "70%", display: "flex", flexWrap: "wrap" }}>
                        {img.map(result => result !== null) ? img.map((result, index) => index === img.length - 1 ? <EndBack list={result}></EndBack> : <EndBack list={result}></EndBack>) : "Error"}
                    </div>
                    <div style={{ width: "100%", height: "30%", backgroundColor: "gray" }}>아직 부족한 점이 많지만 앞으로도 계속 배우며 성장하겠습니다 감사합니다</div>
                </Intro>
            </Section1>
            <Section2></Section2>
        </Body>
    </>
}
export default UseInfo;